import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import RangeSlider from "@vkontakte/vkui/dist/components/RangeSlider/RangeSlider";
import Link from "@vkontakte/vkui/dist/components/Link/Link";
import Home from './Home';
import Favorites from './Favorites';
import { useNavigatorOnline } from '@oieduardorabelo/use-navigator-online';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import FormLayout from '@vkontakte/vkui/dist/components/FormLayout/FormLayout';
import Checkbox from '@vkontakte/vkui/dist/components/Checkbox/Checkbox';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Select from '@vkontakte/vkui/dist/components/Select/Select';
import CellButton from '@vkontakte/vkui/dist/components/CellButton/CellButton';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';


import FormLayoutGroup from '@vkontakte/vkui/dist/components/FormLayoutGroup/FormLayoutGroup';


// import styles from "../styles/App.module.sass";
import styles from "../styles/custom-styles.sass";

const Main = ({
    id, dataStorage, go, activeTab, tabHome, tabFavorites, count, maxDataLength, lastItem, pay,
    getPlatform, story, addGroup, targetList1, storyFavorites, onChangeSlider, startYear, endYear,
    checkBoxOnClick, isAnyYear, genre, handleChange, searchError, tabAddFilms, ref_sourceName,
    refName, updateTheme, share, networkStatus, updateConnection, fetchedUser, openBase
}) => {
    dataStorage = dataStorage.filter(Boolean);

    const renderFavorites = dataStorage.map(item => {
        const year = `${item.year}, IMDb ${item.rating}`;
        return (
            <Favorites
                key={'itemFavorites' + item.id}
                idItem={item.id}
                photo={item.photo}
                name={item.name}
                year={year}
                storyFavorites={storyFavorites}
            // share={share}
            />
        );
    });

    return (

        <Panel id={id}>
            <PanelHeader
                noShadow={true}
            >
                Случайное кино
            </PanelHeader>

            {getPlatform === "ios"
                ?
                <Div className="GroupIos">
                    Данный сервис не работает на устройствах с системой iOS
                </Div>
                :
                <>
                    <FixedLayout vertical="top">
                        <Tabs className="TabsDefaultUser">
                            {/*<HorizontalScroll>*/}
                            <TabsItem
                                onClick={tabHome}
                                selected={activeTab === 'home'}
                                className="tabUser"
                            >
                                Главная
                    </TabsItem>
                            <TabsItem
                                onClick={tabFavorites}
                                selected={activeTab === 'favorites'}
                                className="tabUser"
                            >
                                Избранное
                    </TabsItem>
                            <TabsItem
                                onClick={tabAddFilms}
                                selected={activeTab === 'addFilms'}
                                className="tabUser"
                            >
                                Добавить
                    </TabsItem>

                            {/*</HorizontalScroll>*/}
                        </Tabs>
                    </FixedLayout>
                    
                    {networkStatus
                ?
                <Div className="divSpinner">
                    <Div className="navigatorDiv">
                        Отсутствует интернет - соединение!
                    </Div>
                    <CellButton
                        className="navigator"
                        onClick={updateConnection}
                        align={"center"}
                    >Повторить
                    </CellButton>
                </Div>
                :
                <>
                    {activeTab === 'home' && maxDataLength === false
                        ?
                                <>

                                

                                    <Div>
                                        {lastItem.length !== ''
                                            ?
                                            <Home
                                                key={'itemHome' + lastItem.id}
                                                idItem={lastItem.id}
                                                photo={lastItem.photo}
                                                name={lastItem.name}
                                                year={lastItem.year}
                                            />
                                            :
                                            ''
                                        }

                                        <Div className="SliderDiv">


                                            <FormLayout>
                                                {isAnyYear
                                                    ?
                                                    <RangeSlider
                                                        step={1}
                                                        min={2000}
                                                        max={2019}
                                                        value={[startYear, endYear]}
                                                        onChange={onChangeSlider}
                                                        top="Выберите год"
                                                        bottom={`${startYear} - ${endYear}`}
                                                    />
                                                    :
                                                    <RangeSlider
                                                        className="SliderDefault"
                                                        step={1}
                                                        min={2000}
                                                        max={2019}
                                                        value={[2000, 2019]}
                                                        top="Выберите год"
                                                        bottom={`${2000} - ${2019}`}
                                                    />
                                                }
                                            </FormLayout>


                                            <Checkbox
                                                onClick={checkBoxOnClick}
                                            >
                                                Любой
                                            </Checkbox>
                                        </Div>


                                        {genre === ""
                                            ?
                                            <CellButton level="danger">
                                                Выберите жанр фильма, чтобы продолжить
                                                </CellButton>
                                            :
                                            ""
                                        }


                                        <Select
                                            onChange={handleChange}
                                            placeholder="Выберите жанр">
                                            <option value="Любой">Любой</option>
                                            <option value="боевик">боевик</option>
                                            <option value="история">история</option>
                                            <option value="драма">драма</option>
                                            <option value="приключения">приключения</option>
                                            <option value="криминал">криминал</option>
                                            <option value="мелодрама">мелодрама</option>
                                        </Select>



                                        {searchError
                                            ?
                                            <CellButton level="danger">
                                                Вы получили все фильмы с текущими настройками
                                                фильтра
                                                </CellButton>
                                            :
                                            ""
                                        }

                                        <>

                                        {/* <Button onClick={updateTheme}>Альтернативная тема</Button> */}

                                            <Button
                                                level="commerce"
                                                onClick={openBase}
                                                size="xl"
                                                stretched
                                            >
                                                Найти
                                            </Button>

                                            <Button
                                                className="buttonAddGroup"
                                                level="secondary"
                                                onClick={addGroup}
                                                size="xl"
                                                stretched
                                            >
                                                Подписаться на нашу группу
                                            </Button>
                                            <Button
                                                className="buttonStory"
                                                level="primary"
                                                onClick={story}
                                                size="xl"
                                                stretched
                                            >
                                                Поделиться в истории
                                            </Button>
                                        </>
                                    </Div>
                                </>
                            
                       
                        :
                        <Div>
                            Вы получили все фильмы !
                        </Div>
                    }

                    {activeTab === 'home' && maxDataLength === true
                        ?
                        <Div>
                            Вы получили все фильмы !
                        </Div>
                        :
                        ''
                    }


                    {activeTab === 'favorites'
                        ?
                        <>
                            {dataStorage.length > 0
                                ?
                                <Div>
                                    {renderFavorites}
                                </Div>
                                :
                                <Div>
                                    <Group>
                                        <Div>
                                            Вы еще не получили ни одного фильма.
                                        </Div>
                                    </Group>
                                </Div>
                            }
                        </>
                        :
                        ''
                    }

                    <>
                        {activeTab === 'addFilms'
                            ?

                            <Div>

                                <Group>
                                    <Div>
                                        Здесь вы можете добавить свой любимый фильм в нашу базу.
                                        Для этого нажмите кнопку "Добавить" и напишите название
                                        фильма и его год.
                                    </Div>
                                </Group>

                                <Div>
                                    <Link
                                        href={`https://vk.me/public190534237?ref=${fetchedUser.id}&ref_source=${fetchedUser.id}`}
                                        target="_blank">
                                        {/*<Link href="https://vk.me/public190534237" target="_blank">*/}
                                        <Button
                                            level="commerce"
                                            size="xl"
                                            stretched
                                        >
                                            Добавить
                                        </Button>
                                    </Link>
                                </Div>
                            </Div>
                            :
                            ''
                        }
                    </>
                </>
            }




                </>
            }

            
        </Panel>
    );
};


export default Main;


